.button{display:block;background:#287bbb;color:#fff;border:0;padding:23px 30px 19px;font-size:12px;font-family:"Helvetica Neue Bold", Helvetica, Arial, "Lucida Grande", sans-serif;font-weight:600;text-transform:uppercase;cursor:pointer;width:100%}.button:disabled{background:#dddddd;cursor:default}.button--rounded{width:auto;display:inline-block;border-radius:30px;padding-top:18px;padding-bottom:15px}#sidebar{margin-right:10px;border-right:1px solid #ddd;width:400px;position:relative;background:#e9f2f8}a{display:-webkit-box;display:-webkit-flex;display:-ms-flexbox;display:flex;-webkit-box-align:center;-webkit-align-items:center;-ms-flex-align:center;align-items:center;-webkit-box-pack:center;-webkit-justify-content:center;-ms-flex-pack:center;justify-content:center;width:56px;height:56px;cursor:pointer}a:hover{background:#c9e3f4}.nav{display:-webkit-box;display:-webkit-flex;display:-ms-flexbox;display:flex;padding:0px;border-bottom:1px solid #c2c2c2;z-index:1000}.sidebar-blocks{position:absolute;top:56px;left:0;width:100%;bottom:0;overflow:hidden;box-sizing:border-box;overflow-y:scroll}



