.button{display:block;background:#287bbb;color:#fff;border:0;padding:23px 30px 19px;font-size:12px;font-family:"Helvetica Neue Bold", Helvetica, Arial, "Lucida Grande", sans-serif;font-weight:600;text-transform:uppercase;cursor:pointer;width:100%}.button:disabled{background:#dddddd;cursor:default}.button--rounded{width:auto;display:inline-block;border-radius:30px;padding-top:18px;padding-bottom:15px}#open{max-width:600px;margin:0px auto;text-align:center}.newsletters-list{border-radius:2px 2px 0 0;border:1px solid #eee;border-bottom:none;list-style:none;margin:0px 0;padding:0}.newsletters-list__item{color:#666;padding:10px 20px 8px;border-bottom:1px solid #eee;cursor:pointer}.newsletters-list__item:last-child{border-bottom:none}.newsletters-list__item:hover{background:#eee}.newsletters-list__item.selected{background:rgba(40,123,187,0.3)}



